import React, { PureComponent } from 'react';
import * as actions from './actions/actionIndex';
import store from './store';

export default class Registration extends React.PureComponent {
    constructor() {
        super();
        this.state = {
            screenName: '',
            facebookId: '',
        };
        store.subscribe(() => {
            this.setState({
                facebookId: store.getState().facebookId,
            });
        });
        console.log(1, this.state.facebookId);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
        console.log(2, store.getState().facebookId);
    }
    handleSubmit(event) {
        event.preventDefault();
        const newPlayer = {
            facebookId: this.state.facebookId,
            screenName: this.state.screenName,
        };
        console.log(3, newPlayer);
        actions.newPlayerRegistration(newPlayer);
    }

    handleChange(event) {
        const screenName = event.target.value;
        this.setState({ screenName: screenName });
        console.log(4, screenName);
    }

    render() {
        return (
            <div>
                <p>Please enter a screen name</p>
                <input type="text" onChange={this.handleChange} />
                <input
                    type="submit"
                    value="submit"
                    onClick={this.handleSubmit}
                />
            </div>
        );
    }
}



